  padding-bottom: 20px;
  font-size: 1.75rem;
  font-weight: 500;
  line-height: 1.2;
}

.custom-control-label {
  font-weight: 400;
}



.form-group {
    margin-bottom: 1rem;
    >label{
        color: #FFC312 ;
    }
}
.form-inline .form-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    -ms-flex-align: center;
    align-items: center;
    margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
