body { padding:0; margin:0; }
.container { 
    width:100%; 
    height:100vh;
    // background-image:radial-gradient( rgb(6, 6, 51),rgb(4, 4, 44),rgb(2, 2, 22), rgb(1, 1, 8)); 
    background: #b7ddff;
    color: #252525;
    position:relative;  
    overflow: hidden; 
}
.stars-wrapper {     overflow: hidden;    position: absolute;    width: 100%;   height: 100%;    z-index: 0; color:#fff; }
.stars-wrapper .left-constellation { position: absolute;    top: 0;    left: 0;    width: 50%;    height: 100% }
.stars-wrapper .right-constellation { position: absolute;    top: 0;    left: 50%;    width: 50%;    height: 100% }
.stars-near, .stars-middle, .stars-far { position: absolute;    width: 200%;    height: 100%;    -webkit-transform: translateX(-50%);    transform: translateX(-50%);}
.stars-near { -webkit-animation: starFieldAnimation 200s linear infinite; animation: starFieldAnimation 200s linear infinite; }
.stars-middle { -webkit-animation: starFieldAnimation 400s linear infinite; animation: starFieldAnimation 400s linear infinite; }
.stars-far { -webkit-animation: starFieldAnimation 600s linear infinite;    animation: starFieldAnimation 600s linear infinite; }
.star {  fill: #4f5660 }

.arwing-wrapper { height: 363px; left: 60%; overflow: visible; position: absolute; top: 20%; width: 40%; }
.arwing { -webkit-animation: arwingAnim 7s ease-in-out infinite;    animation: arwingAnim 7s ease-in-out infinite;    width: 300px;   -webkit-transform: translateY(0);    transform: translateY(0); }

.wave { display:block; position:absolute; width:100%; bottom:0;  }
.wavePath { animation: waveDividerAnimation 20s linear infinite alternate; fill:rgba(255,255,255,0.3); }


